import React, { useEffect, useState } from 'react';
import Sidebar from '../components/Sidebar';
import { getReservations } from '../services/reservation';
import { Reservation } from '../types/reservation';
import { toast } from 'react-toastify';
import Chart from 'chart.js/auto';
import { saveAs } from 'file-saver';
import styles from '../styles/Dashboard.module.css';

const Dashboard: React.FC = () => {
  const [reservations, setReservations] = useState<Reservation[]>([]);
  const [filteredReservations, setFilteredReservations] = useState<Reservation[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [searchQuery, setSearchQuery] = useState<string>('');
  const [filterStatus, setFilterStatus] = useState<string>('all');
  const [currentPage, setCurrentPage] = useState<number>(1);
  const itemsPerPage = 5;

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        const reservationsData = await getReservations();
        setReservations(reservationsData);
        setFilteredReservations(reservationsData);
        renderChart(reservationsData);
      } catch (error) {
        toast.error('Erro ao carregar as reservas!');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    let updatedReservations = reservations;

    if (filterStatus !== 'all') {
      updatedReservations = updatedReservations.filter(
        (res) => res.status.toLowerCase() === filterStatus.toLowerCase()
      );
    }

    if (searchQuery) {
      updatedReservations = updatedReservations.filter((res) =>
        res.environment.toLowerCase().includes(searchQuery.toLowerCase())
      );
    }

    setFilteredReservations(updatedReservations);
  }, [filterStatus, searchQuery, reservations]);

  const renderChart = (data: Reservation[]) => {
    const environments = Array.from(new Set(data.map((res) => res.environment)));
    const counts = environments.map((env) =>
      data.filter((res) => res.environment === env).length
    );

    const ctx = document.getElementById('reservationChart') as HTMLCanvasElement;
    if (ctx) {
      new Chart(ctx, {
        type: 'bar',
        data: {
          labels: environments,
          datasets: [
            {
              label: 'Reservas por Ambiente',
              data: counts,
              backgroundColor: 'rgba(75, 192, 192, 0.2)',
              borderColor: 'rgba(75, 192, 192, 1)',
              borderWidth: 1,
            },
          ],
        },
        options: {
          responsive: true,
          plugins: {
            legend: {
              display: false,
            },
          },
        },
      });
    }
  };

  const handleDownloadReport = () => {
    const csvContent =
      'data:text/csv;charset=utf-8,' +
      ['Ambiente,Data,Status']
        .concat(
          filteredReservations.map(
            (res) =>
              `${res.environment},${new Date(res.date).toLocaleString()},${res.status}`
          )
        )
        .join('\n');

    const encodedUri = encodeURI(csvContent);
    saveAs(encodedUri, 'reservations_report.csv');
  };

  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentReservations = filteredReservations.slice(indexOfFirstItem, indexOfLastItem);

  const handlePageChange = (page: number) => setCurrentPage(page);

  return (
    <div className={styles.dashboardContainer}>
      <Sidebar />
      <div className={styles.dashboardContent}>
        <h1 className={styles.dashboardTitle}>Dashboard de Reservas</h1>

        {/* Filtros e Barra de Busca */}
        <div className={styles.filtersContainer}>
          <input
            type="text"
            placeholder="Buscar por ambiente..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className={styles.searchInput}
          />
          <select
            value={filterStatus}
            onChange={(e) => setFilterStatus(e.target.value)}
            className={styles.filterSelect}
          >
            <option value="all">Todos os Status</option>
            <option value="pendente">Pendente</option>
            <option value="concluido">Concluído</option>
            <option value="cancelado">Cancelado</option>
          </select>
          <button
            className={styles.downloadButton}
            onClick={handleDownloadReport}
          >
            Baixar Relatório
          </button>
        </div>

        {/* Gráfico de Resumo */}
        <div className={styles.chartContainer}>
          <canvas id="reservationChart"></canvas>
        </div>

        {/* Tabela de Reservas */}
        <div className={styles.tableContainer}>
          <h2>Últimas Reservas</h2>
          {loading ? (
            <p className={styles.loading}>Carregando...</p>
          ) : (
            <>
              <table className={styles.table}>
                <thead>
                  <tr>
                    <th>Ambiente</th>
                    <th>Data</th>
                    <th>Status</th>
                    <th>Ações</th>
                  </tr>
                </thead>
                <tbody>
                  {currentReservations.map((reservation) => (
                    <tr key={reservation.id}>
                      <td>{reservation.environment}</td>
                      <td>{new Date(reservation.date).toLocaleString()}</td>
                      <td>{reservation.status}</td>
                      <td>
                        <button
                          className={styles.button}
                          onClick={() =>
                            alert(`Editando reserva de ${reservation.environment}`)
                          }
                        >
                          Editar
                        </button>
                        <button
                          className={`${styles.button} ${styles.buttonMarginLeft}`}
                          onClick={() =>
                            alert(`Cancelando reserva de ${reservation.environment}`)
                          }
                        >
                          Cancelar
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
              <div className={styles.pagination}>
                {Array.from(
                  { length: Math.ceil(filteredReservations.length / itemsPerPage) },
                  (_, i) => (
                    <button
                      key={i}
                      onClick={() => handlePageChange(i + 1)}
                      className={`${styles.pageButton} ${
                        currentPage === i + 1 ? styles.activePage : ''
                      }`}
                    >
                      {i + 1}
                    </button>
                  )
                )}
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
